@import url(https://fonts.googleapis.com/css?family=Montserrat:100,400,900&display=swap);
body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.menu ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.menu ul li a {
  width: 100%;
  padding: 10px;
  background: #111111;
  display: block;
  color: #ffffff;
  font-size: 0.9em;
  -webkit-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
  margin: 5px 0;
  border-radius: 0 20px 20px 0;
}

.menu ul li a:hover {
  background: #555555;
  text-decoration: none;
  color: #ffffff;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.Home {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.Home .row.Contenidos {
  flex: 1 1;
}

.Home .row.Footer {
  background: #000000;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #ffffff;
}

.Home .row.Footer p {
  font-size: 0.8em;
  margin: 0;
  padding: 10px;
  text-align: center;
}

.Home .col-md-3.col-3 {
  height: 100vh;
  display: flex;
  align-items: center;
}

ul.temas {
  width: 100%;
  list-style: none;
  display: flex;
  flex-direction: row;
  margin: 15px 0;
  padding: 0;
}

ul.temas li {
  flex-grow: 1;
  text-align: center;
  margin: 1px;
}

ul.temas li a {
  background:#000000;
  padding: 10px;
  width: 100%;
  display: block;
  color: #ffffff;
  border-radius: 20px;
  -webkit-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}

ul.temas li a:hover {
  background: red;
  text-decoration: none;
}


.home {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 90vh;
  text-align: center;
}

.home h2 {
  font-size: 1.5em;
  font-weight: bold;
}
